  --snowfall-pos-y: 0;
  background-repeat: repeat;
  background-position: 0 0;
}

/* no-op condition for scoping */
html::after {
  animation: snowfall-no-composition 1920s linear infinite;
}

@keyframes snowfall-no-composition {
  0% {
    background-position: 0 0, 0 0, 0 0;
  }
  0.625% {
    background-position: -12px 480px, -9px 360px, -4.8px 192px;
  }
  33.3333333333% {
    background-position: -640px 25600px, -480px 19200px, -256px 10240px;
  }
  100% {
    background-position: -1920px 76800px, -1440px 57600px, -768px 30720px;
  }
}
@supports (animation-composition: replace, add, add) {
  /**/
  html::after {
    animation: snowfall-x 640s linear infinite, snowfall-y 12s linear infinite, snowfall-x-wobble-1 8s cubic-bezier(0.35, 0, 0.65, 0.99) infinite, snowfall-x-wobble-2 8s cubic-bezier(0.35, 0, 0.65, 0.99) infinite;
    animation-composition: replace, add, add, add;
    animation-delay: 0s, 0s, 0s, 0.23s;
  }
}
@keyframes snowfall-y {
  from {
    background-position: 0 0, 0 0, 0 0;
  }
  to {
    background-position: 0 480px, 0 360px, 0 192px;
  }
}
@keyframes snowfall-x {
  from {
    background-position: 0 0, 0 0, 0 0;
  }
  to {
    background-position: -640px 0, -480px 0, -256px 0;
  }
}
@keyframes snowfall-x-wobble-1 {
  0% {
    background-position: 16px 0;
  }
  50% {
    background-position: -16px 0;
  }
  100% {
    background-position: 16px 0;
  }
}
@keyframes snowfall-x-wobble-2 {
  0% {
    background-position: 0 0, 12px 0;
  }
  50% {
    background-position: 0 0, -12px 0;
  }
  100% {
    background-position: 0 0, 12px 0;
  }
}
@keyframes snowfall-x-wobble {
  0% {
    background-position: 16px 0, 12px 0, 6.4px 0;
  }
  50% {
    background-position: -16px 0, -12px 0, -6.4px 0;
  }
  100% {
    background-position: 16px 0, 12px 0, 6.4px 0;
  }
}
/* theming */
html {
  background: #223232;
  color: #FFF;
  font-family: Calibri, sans-serif;
  padding: 2em;
}

main {
  margin: 0 auto;
  max-width: 960px;
  padding: 1em;
  background: #223232;
  border: 1em solid #222;
}

*:first-child {
  margin-top: 0;
}

*:last-child {
  margin-bottom: 0;
}

ul > li {
  margin: 1em 0;
}

a:link {
  color: #7AF;
}

a:visited {
  color: #C5F;
}

pre {
  padding: 0.5em;
  border: 1px solid #222;
  margin: 1em 2em;
}

section + section {
  margin-top: 1em;
  padding-top: 1em;
  border-top: 2px solid #222;
}